      Cookie文件是存储了一些与用户访问网站有关信息的文件，格式为 .txt。
      Cookie内容通常均经过加密。安全的
      Cookie技术会为用户在上网时提供更为便捷的服务。
    </p>
    <p>
      <strong>IP 地址：</strong>每台上网的设备都会指定一个编号，称为互联网协议
      (IP)地址。这些编号通常都是根据地理区域指定的。
      IP地址通常可用于识别设备连接至互联网时所在的位置。
    </p>
    <p>
      <strong>GPS ：</strong>Global Positioning System
