      >即公用移动通信基站，是无线电台站的一种形式，是指在一定的无线电覆盖区中，通过移动通信交换中心，与移动电话终端之间进行信息传递的无线电收发信电台。
    </p>
    <p>
      <strong>TLS ：</strong>TLS（ Transport Layer
      Security）又称安全传输层协议，由 TLS 记录协议（ TLS Record）和 TLS
      握手协议（ TLS
      Handshake）组成，用于在两个通信应用程序之间提供保密性和数据完整性。
    </p>
    <p>
      <strong>SSL ：</strong>SSL （ Secure Socket
      Layer）又称为安全套接层，是在传输通信协议（ TCP/IP）上实现的一种安全协议。
      SSL支持各种类型的网络，同时提供三种基本的安全服务，均通过使用公开密钥和对称密钥技术以达到信息保密的效果。
    </p>
